        <template v-if="store.state.site.lang === 'ko'">
          <div class="alert alert-warning">{{ env.VUE_APP_ALERT_KO }}</div>
          <div>다음의 순서에 따라 진행하시면 페이지를 생성하고 추출하실 수 있습니다.</div>
